<template>
  <div class="">
    <navbar @accountChanged="accountChanged"></navbar>
    <div id="pool-bet">
      <div id="pool">
        <b-container>
          <b-row class="d-flex justify-content-start justify-content-md-end mt-1">

            <b-navbar-nav class="mr-auto ml-3 ml-md-3">
              <b-nav-item-dropdown :text="$t('lang.language')" left>
                <b-dropdown-item @click="changeLanguage(item.value)" v-for="(item, index) in langList" :key="index">
                  {{ item.name }}
                </b-dropdown-item>
              </b-nav-item-dropdown>
              <!--              <select v-model="$i18n.locale" class="form-control">-->
              <!--                <option v-for="(item, index) in langList" :key="index" :value="item.value">-->
              <!--                  {{ item.name }}-->
              <!--                </option>-->
              <!--              </select>-->
              <!--              <v-google-translate :languages="languages" default-language-code="zh-CN"></v-google-translate>-->
            </b-navbar-nav>
            <!--            <v-google-translate :languages="languages" ></v-google-translate>-->
            <b-navbar-nav class="ml-auto mr-1 mr-md-4">
              <button class="btn btn-warning px-5" @click="withdraw">{{ $t('pool.withdraw') }}</button>
            </b-navbar-nav>
          </b-row>
          <b-row align-h="center" class="pool-block">
            <b-col cols="12">
              <h4 class="symbol-name notranslate">{{ $t('pool.title') }}</h4>
            </b-col>
            <b-col cols="12">
              <h1 class="pool-amount text-flashy notranslate">${{ T2U(pool) }}</h1>
            </b-col>
          </b-row>
          <b-row align-h="center">
            <h2 class="pool-text d-block">{{ $t('pool.pool') }}</h2>
          </b-row>
          <b-row align-h="center" class="mt-5 roll">
            <b-col cols="12" md="5" class="d-flex justify-content-center justify-content-md-end  turntable" order="1">
              <!--              <b-img src="/assets/img/12.png" fluid></b-img>-->
              <!-- 大转盘抽奖 -->
              <LuckyWheel
                  ref="luckyWheel"
                  width="300px"
                  height="300px"
                  :blocks="blocks"
                  :prizes="prizes"
                  :buttons="buttons"
                  :defaultStyle="defaultStyle"
                  @end="endCallBack"
              />
            </b-col>
            <b-col md="6" class="countdown d-flex justify-content-lg-start justify-content-center align-items-center"
                   order="4" order-md="2">
              <!--            <b-img src="/assets/img/4.png" fluid ></b-img>-->
              <div class="countdown-border d-flex flex-column justify-content-between">
                <div class="title">{{ $t('pool.countdown') }}</div>
                <div class="arrow align-self-center"></div>
                <div class="" v-html="$t('pool.round', {round: round})"></div>
                <div class="d-flex flex-row justify-content-around align-items-center my-2">
                  <timer-down ref="timerDown" @countDownEnd="countDownEnd" @timeTick="timeTick"></timer-down>
                </div>
                <div class="info"
                     v-html="$t('pool.info', {hasRound: todayRound - 1, leftRound: 288 - todayRound + 1})"></div>
              </div>
              <div class="right-block">
              </div>
            </b-col>
            <b-col cols="12" md="5" class=" result" order="2" order-md="3">
              <p>{{ $t('pool.lastNumber') }}<strong v-show="winningNumber !== -1">{{ winningNumber }}</strong><span
                  :class="{'text-green': isDouble(winningNumber), 'text-red': isSingle(winningNumber)}">{{
                  number2Text(winningNumber)
                }}</span><span v-show="winningNumber !== -1">{{ $t('pool.winText') }}</span></p>
            </b-col>
            <b-col md="6" class=" reward" order="3" order-md="4">
              <p>{{ $t('pool.lastMaxPrize') }}
                <br class="d-block d-md-none"/>
                <strong>{{ lastMaxPrize }}</strong>{{ symbol }}&emsp;≈&emsp;<strong>{{ T2U(lastMaxPrize) }}</strong>U
              </p>
            </b-col>
          </b-row>
        </b-container>
        <b-img src="/assets/img/2.png" fluid style="margin-top: 0; margin-bottom: 0; padding-bottom: 0"></b-img>
      </div>
      <div id="bet">
        <b-container>
          <div class="d-flex flex-column flex-md-row justify-content-center">
            <div class="text-right mb-md-0 mb-2 mr-md-1 roundList">
              <div style="font-size: .95rem;" class="text-white text-center">{{ $t('bet.hashLink') }}</div>
              <prize-list ref="roundList"></prize-list>
            </div>
            <div class="text-left ml-md-1 d-flex  justify-content-center align-items-center exchange">
              <div class="text-white text-center title">{{ $t('bet.exchange') }}</div>
              <button class="exchange-btn" @click="$bvModal.show('recharge')">{{ $t('bet.click') }}</button>
            </div>
          </div>
          <b-row class="my-3">
            <b-col cols="12" md="6">
              <b-row>
                <b-col class="col-6 col-md-4 offset-md-4">
                  <div class="bet-item cursor bg-red" :class="{active:flow === 1}" @click="betFlow(1)">
                    {{ $t('number.singleText') }}
                  </div>
                </b-col>
                <div class="col-6 col-md-4">
                  <div class="bet-item cursor bg-green" :class="{active:flow === 0}" @click="betFlow(0)">
                    {{ $t('number.doubleText') }}
                  </div>
                </div>
                <b-col cols="12" class="my-3 text-white text-md-right"
                       v-html="$t('bet.volume', {singleNumber: singleNumbers, doubleNumber: doubleNumbers})">

                </b-col>
              </b-row>
            </b-col>
            <b-col cols="12" md="6" class="buy">
              <div class="d-flex flex-column justify-content-center align-items-center">
                <div class="number ">
                  <div @click="number > 1 ? number-- : 0" class="click-press">-</div>
                  <input v-model="number" class="align-self-stretch"/>
                  <div @click="number++" class="click-press">+</div>
                </div>
                <div class="mt-1" v-html="$t('bet.chip', {chip})">

                </div>
              </div>
              <div>
                <b-btn class="px-4 align-self-stretch" style="font-size: 1.5rem; background: #f7931a"
                       :disabled="!isCanBet"
                       @click="$bvModal.show('buy')">{{ $t('bet.buy') }}<span v-show="!isCanBet">({{ second }})</span>
                </b-btn>
                <div class="mt-1">
                  {{ $t('bet.lastMaxPrize') }} <b class="text-warning">{{ T2U(lastMaxPrize) }}</b> USDT
                </div>
              </div>
            </b-col>
          </b-row>

          <div class="row">
            <div class="col-12 col-md-6">
              <p class="text-md-right" v-html="$t('bet.balance', {balance, usdtBalance: T2U(balance)})"></p>
            </div>
            <div class="col-12 col-md-6">
              <p class="text-md-left"><span v-show="mySinglePool > 0 || myDoublePool > 0">{{ $t('bet.yourBet') }}</span><span
                  v-show="mySinglePool > 0">{{ $t('number.singleText') }}:<b
                  class="text-red">{{ mySinglePool }}</b> {{ symbol }}</span>
                <span v-show="myDoublePool > 0" style="margin-left: 1rem">{{ $t('number.doubleText') }}:<b
                    class="text-green">{{ myDoublePool }}</b> {{ symbol }}</span></p>
            </div>
          </div>

          <b-row class="arrow mt-1" align-h="center">
            <img src="/assets/img/6.png" style="position: absolute; bottom: -5rem"/>
          </b-row>
        </b-container>
      </div>
      <div>
        <b-modal id="recharge" :title="$t('modal.recharge.title')" :ok-title="$t('modal.ok')"
                 :cancel-title="$t('modal.cancel')" @ok="rechargeOk">
          <input type="text" v-model="rechargeAmount" class="p-2 border" style="border-radius: 4rem">{{ symbol }}
          <p class="mt-3" v-html="$t('modal.recharge.needPay', {amount: T2U(rechargeAmount)})"></p>
          <p class="mt-3" v-html="$t('modal.recharge.balance', {balance})"></p>
        </b-modal>
        <b-modal id="withdraw" :title="$t('modal.withdraw.title')" :ok-title="$t('modal.ok')"
                 :cancel-title="$t('modal.cancel')" @ok="withdrawOk">
          <p>{{ $t('modal.withdraw.amount') }}：{{ symbol }}</p>
          <input type="text" v-model="withdrawAmount" class="p-2 border" style="border-radius: 4rem">
          <strong style="background: #f7931a; color: #fff; padding: .5rem 1rem; cursor: pointer"
                  @click="withdrawAmount = balance">{{ $t('modal.withdraw.max') }}</strong>
          <p class="mt-3" v-html="$t('modal.withdraw.prize', {amount: T2U(withdrawAmount)})"></p>
          <p class="mt-3" v-html="$t('modal.withdraw.balance', {balance})"></p>
        </b-modal>
        <b-modal id="message" :title="$t('modal.message.title')" :ok-title="$t('modal.ok')"
                 :cancel-title="$t('modal.cancel')">
          <p class="my-4">{{ message }}</p>
        </b-modal>
        <b-modal id="buy" :title="$t('modal.bet.title')" :ok-title="$t('modal.ok')" :cancel-title="$t('modal.cancel')"
                 @ok="bet">
          <p class="my-4">{{ $t('modal.bet.flow') }}：<span
              :class="{'text-red': isSingle(this.flow), 'text-green': isDouble(this.flow)}">{{ flow2Text }}</span></p>
          <p class="my-4">{{ $t('modal.bet.amount') }}：<span style="font-weight: bold; color: #f60;">{{ amount }}</span>
            {{ symbol }}</p>

          <p class="my-4" style="font-size: .95rem; color: #817e7e"
             v-html="$t('modal.bet.prize', {estimatedBonus})"></p>
        </b-modal>
        <b-modal id="prize-message" :title="$t('modal.prize.title')" :ok-title="$t('modal.ok')"
                 :cancel-title="$t('modal.cancel')">
          <p class="my-4">{{ $t('modal.prize.round') }}：<span
              style="font-weight: bold; color: #1dd7d7;">{{ round - 1 }}</span></p>
          <p class="my-4">{{ $t('modal.prize.number') }}：<span style="font-weight: bold; color: #f60;">{{
              winningNumber
            }}</span></p>
          <p class="my-4">{{ $t('modal.prize.flow') }}：<span
              :class="{'text-red': isSingle(winningNumber), 'text-red': isDouble(winningNumber)}">{{
              number2Text(winningNumber)
            }}</span>
          </p>
        </b-modal>
      </div>
    </div>
  </div>

</template>

<script>
import PrizeList from "@/components/PrizeList";
import TimerDown from "@/components/TimerDown";
import Navbar from "@/components/Navbar";
import Data from "@/components/Contract";
import {LuckyWheel, LuckyGrid} from 'vue-luck-draw'
import {languages} from "@/javasript/languages";
import axios from "axios";

export default {
  name: "PoolBet",
  components: {Navbar, TimerDown, PrizeList, LuckyWheel, LuckyGrid},
  data() {
    return {
      symbol: 'chipsBTC',
      winningNumber: -1, //上期中奖号码
      intervalMinute: 5, //每期间隔
      lastMaxPrize: 0, //上期最高得奖额度
      pool: 0, //{{symbol}} 额度
      singlePool: 0, //{{symbol}} 额度
      doublePool: 0, //{{symbol}} 额度
      mySinglePool: 0, //{{symbol}} 我的单下注额度
      myDoublePool: 0, //{{symbol}} 我的双下注额度
      flow: 1, //下注方向, 1:单， 0：双
      chip: 1000, //当前每注金额
      number: 1, //当前下注份数
      rechargeAmount: 10000, //充币金额
      withdrawAmount: 10000, //提币金额
      message: 'message', //消息提示
      account: '',
      balance: 0, //用户余额
      usdtBalance: 0, //用户余额
      usdtContract: new web3.eth.Contract(Data.usdtAbi, Data.usdtAddress), //USDT合约对象
      tokenContract: new web3.eth.Contract(Data.tokenAbi, Data.tokenAddress), //代币合约对象,
      isCanBet: true, //当前是否能下注
      second: 0, //下注提示倒计时
      round: 0,
      todayRound: 0,
      blocks: [
        {padding: '13px', background: '#d64737'}
      ],
      prizes: [
        {title: '0', background: '#ef7580', fonts: [{text: '0', top: '18%'}]},
        {title: '1', background: '#d8d5dc', fonts: [{text: '1', top: '18%'}]},
        {title: '2', background: '#fff73c', fonts: [{text: '2', top: '18%'}]},
        {title: '3', background: '#f7931a', fonts: [{text: '3', top: '18%'}]},
        {title: '4', background: '#ef7580', fonts: [{text: '4', top: '18%'}]},
        {title: '5', background: '#d8d5dc', fonts: [{text: '5', top: '18%'}]},
        {title: '6', background: '#fff73c', fonts: [{text: '6', top: '18%'}]},
        {title: '7', background: '#f7931a', fonts: [{text: '7', top: '18%'}]},
        {title: '8', background: '#fff73c', fonts: [{text: '8', top: '18%'}]},
        {title: '9', background: '#f7931a', fonts: [{text: '9', top: '18%'}]},
      ],
      buttons: [
        {radius: '55px', background: '#d64737'},
        {radius: '50px', background: '#fff'},
        {radius: '45px', background: '#f6c66f', pointer: true},
        {
          radius: '40px', background: '#ffdea0',
          fonts: [{text: this.$t('pool.prizeNumber'), fontSize: '1rem', top: -18}]
        }
      ],
      defaultStyle: {
        fontColor: '#064f4f',
        fontSize: '1.5rem',
        fontWeight: 'bold'
      },
      languages: languages,
      defaultLanguageCode: 'en',
      langList: [
        {name: 'English', value: 'en'},
        {name: '简体中文', value: 'zh-cn'},
      ]
    }
  },
  async created() {
    this.tokenContract.events.Bet({}, (err, result) => {
      if (result === null) return;
      let data = result.returnValues;
      this.pool = web3.utils.fromWei(data.pool, 'ether');
      this.singlePool = web3.utils.fromWei(data.singlePool, 'ether')
      this.doublePool = web3.utils.fromWei(data.doublePool, 'ether')
      if (data.sender.toLowerCase() === this.account.toLowerCase()) {
        //更新用户信息
        this.userInit()
      }
    })

    //不由事件开奖。由倒计时开奖
    // this.tokenContract.events.prize({}, (err, result) => {
    //   this.init()
    // })



    //使用
    // fetch('/api/rest/api3.do?api=mtop.common.getTimestamp', {
    //   method: 'post',
    //   credentials: 'include',
    //   headers: {
    //     'Content-Type': 'application/json;charset=UTF-8'
    //   }
    // }).then().then(res=>console.log(res))

    //更新回合
    this.todayRound = await this.getTodayRound();
    this.round = await this.getRound();
    //初始化
    await this.init()
  },
  methods: {
    // 点击抽奖按钮触发回调
    async startCallBack() {
      this.increaseRound();
      await this.init()
      if (this.winningNumber > -1) {
        // 先开始旋转
        this.$refs.luckyWheel.play()
        // 使用定时器来模拟请求接口
        setTimeout(() => {
          // 3s后得到中奖索引
          // 缓慢停止游戏
          this.$refs.luckyWheel.stop(this.winningNumber)
        }, 3000)
      } else {
        this.$bvModal.show('prize-message')
      }
    },
    // 抽奖结束触发回调
    async endCallBack(prize) {
      //如果未开奖走下面逻辑。
      this.$bvModal.show('prize-message')
    },
    async timeTick(seconds) {
      console.log("second: " + seconds);
      this.isCanBet = seconds >= 30;
      this.second = seconds;
    },
    countDownEnd(result) {
      //弹出开机窗口
      console.log('countDownEnd');
      this.startCallBack()
    },
    increaseRound() {
      this.todayRound++;
      this.round++;
    },
    //登录并获取账户
    accountChanged(account) {
      this.account = account
      //获取代币余额+
      this.userInit()
    },
    //获取账户余额--代币
    getBalance() {
      if (this.account === '') return;
      this.tokenContract.methods.balanceOf(this.account).call((err, result) => {
        this.balance = this.fromWeiByEther(result)
      })
      this.usdtContract.methods.balanceOf(this.account).call((err, result) => {
        this.usdtBalance = this.fromWeiByEther(result)
      })
    },
    async getPoolInfo() {
      //pool
      this.tokenContract.methods.pool().call((err, result) => {
        this.pool = this.fromWeiByEther(result)
      })

      //singlePool
      this.tokenContract.methods.singlePool().call((err, result) => {
        this.singlePool = this.fromWeiByEther(result)
      })

      //doublePool
      this.tokenContract.methods.doublePool().call((err, result) => {
        this.doublePool = this.fromWeiByEther(result)
      })

      //最高得奖金额
      // this.tokenContract.methods.lastMaxPrize().call((err, result) => {
      //   this.lastMaxPrize = this.fromWeiByEther(result)
      // })
      let result = await this.tokenContract.methods.lastMaxPrize().call()
      this.lastMaxPrize = this.fromWeiByEther(result)

      //开奖号码.. 移到最新3期中
      // this.tokenContract.methods.winningNumber().call((err, result) => {
      //   this.winningNumber = parseInt(result)
      // })
      // let _winningNumber = await this.tokenContract.methods.winningNumber().call();
      // this.winningNumber = parseInt(_winningNumber);


      //获取最新的3条开奖记录
      await this.getRoundList()
    },
    async init() {


      //获取用户相关信息
      this.userInit()

      //获取注池相关信息
      await this.getPoolInfo()

    },
    userInit() {
      //获取代币余额+
      this.getBalance()
      //获取我的下注
      this.getMyBet()
    },
    async getRoundList() {
      let roundList = [];
      // let rd = await this.getRound()
      // let round = parseInt(rd.toString()) - 3;
      for (let i = 5; i > 0; i--)
      {
        let rd = this.round - i;
        let block = await this.tokenContract.methods.roundMap(rd).call();
        if (block){
          block = 'https://bscscan.com/tx/' + block;
        } else {
          block = this.$t('pool.noPrize')
        }
        let item = {
          'round': rd,
          'block': block
        }
        roundList.push(item)

        if (i === 1)
        {
          //上期
          this.winningNumber = this.getWinningNumber(block)
        }
      }

      this.$refs.roundList.listData = roundList

    },
    //获取我的下注
    getMyBet() {
      if (this.account === '') return;
      this.tokenContract.methods.singleOf(this.account).call((err, result) => {
        this.mySinglePool = this.fromWeiByEther(result)
      })

      //我的下注金额
      this.tokenContract.methods.doubleOf(this.account).call((err, result) => {
        this.myDoublePool = this.fromWeiByEther(result)
      })
    },
    betFlow(value) {
      if (value === 0) {
        this.flow = 0;
      }
      if (value === 1) {
        this.flow = 1;
      }
    },
    rechargeOk() {
      if (this.usdtBalance < this.T2U(this.rechargeAmount)) {
        this.message = this.$t('message.wallet');
        this.$bvModal.show('message')
        return false;
      }

      //USDT授权
      let amount = web3.utils.toWei(this.rechargeAmount.toString(), 'ether')
      let usdtAmount = web3.utils.toWei(this.T2U(this.rechargeAmount).toString(), 'ether')

      //查询是否有授权。有的话。就直接充。无需重复授权。
      this.usdtContract.methods.allowance(this.account, Data.tokenAddress).call((err, result) => {
        if (web3.utils.fromWei(result, 'ether') < this.T2U(this.rechargeAmount)) {
          //授权并充值
          this.usdtContract.methods.approve(Data.tokenAddress, usdtAmount).send({from: this.account}).then(receipt => {
            this.tokenRecharge(amount);
          }).catch(err => {
            this.message = this.$t('message.empower');
            this.$bvModal.show('message');
          });
        } else {
          //直接充
          this.tokenRecharge(amount)
        }
      })
    },
    //代币充值
    tokenRecharge(amount) {
      // console.log(this.account);
      // console.log(amount);
      this.tokenContract.methods.recharge(amount).send({from: this.account}).then(res => {
        this.message = this.$t('message.rechargeSuccess');
        this.$bvModal.show('message');
        this.getBalance();
      }).catch(err => {
        this.message = this.$t('message.rechargeError');
        this.$bvModal.show('message');
      });
    },
    withdraw() { //withdrawBtn
      this.withdrawAmount = this.balance;
      this.$bvModal.show('withdraw')
    },
    withdrawOk() {
      let amount = web3.utils.toWei(this.withdrawAmount.toString(), 'ether');
      this.tokenContract.methods.withdraw(amount).send({from: this.account}).then(res => {
        this.message = this.$t('message.withdrawSuccess');
        this.$bvModal.show('message');
        this.getBalance();
      }).catch(err => {
        this.message = this.$t('message.withdrawError');
        this.$bvModal.show('message');
      })
    },
    //投注
    bet() {
      if (this.balance < this.amount) {
        this.message = this.$t('message.notEnough');
        this.$bvModal.show('message')
        return false;
      }
      let chips = web3.utils.toWei(this.amount.toString(), 'ether')

      this.tokenContract.methods.bet(this.flow, chips).send({from: this.account}).then(res => {
        this.message = this.$t('message.betSuccess');
        this.$bvModal.show('message');
        this.getBalance();
      }).catch(err => {
        this.message = this.$t('message.betError');
        this.$bvModal.show('message');
      })
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      localStorage.setItem('lang', lang);
      location.reload()
    },
    fromWeiByEther(num) {
      if (num) {
        return parseFloat(web3.utils.fromWei(num, 'ether'))
      }
      return 0;
    },
    formatNum(num) {
      return num < 10 ? '0' + num : '' + num
    },
    formatRound(num) {
      if (num < 10) {
        return '00' + num;
      }
      if (num < 100) {
        return '0' + num
      }
      return '' + num;
    },
    async getTodayRound() {
      // let nowTime = await this.tokenContract.methods.timestamp().call();
      let res = await axios.get('/api/rest/api3.do?api=mtop.common.getTimestamp');
      let nowTime = parseInt(res.data.data.t) / 1000 + 28800;
      return Math.floor((nowTime % 86400) / (this.intervalMinute * 60)) + 1;
      // return Math.floor((nowTime.getHours() * 60 + nowTime.getMinutes()) / this.intervalMinute) + 1;
    },
    async getRound() {
      let nowTime = new Date();
      let year = nowTime.getFullYear()
      let month = nowTime.getMonth() + 1
      let day = nowTime.getDate()
      let rd = await this.getTodayRound()
      return parseInt(year + this.formatNum(month) + this.formatNum(day) + this.formatRound(rd))
    },
    getWinningNumber(hash) {
      let charArr = hash.split('');
      for (let i = charArr.length - 1; i >= 0; i--) {
        if (/\d/.test(charArr[i])) {
          return parseInt(charArr[i])
        }
      }
      return -1;
    },
  },
  computed: {
    //代币转USDT
    T2U() {
      return value => value / 100;
    },
    //USDT转代币
    U2T() {
      return value => value * 100;
    },
    //开奖数转文字
    number2Text() {
      return value => value === -1 ? this.$t('number.noneText') : (value % 2 === 0 ? this.$t('number.doubleTexts') : this.$t('number.singleTexts'))
    },
    //投注方向
    flow2Text() {
      return this.flow === 1 ? this.$t('number.singleTexts') : this.$t('number.doubleTexts')
    },
    //数字是否为单
    isSingle() {
      return value => value % 2 === 1;
    },
    //数字是否为双
    isDouble() {
      return value => value % 2 === 0;
    },
    amount() {
      return this.chip * this.number;
    },
    singleNumbers() {//当前下单人数
      return this.singlePool / this.chip;
    },
    doubleNumbers() { //当前下双人数
      return this.doublePool / this.chip;
    },
    //预估奖金
    estimatedBonus() {
      // if (this.pool === 0) return 0;
      let amount = this.chip * this.number;
      if (this.flow === 1) { //单
        if (this.doublePool === 0) {
          //对方无人下注
          return amount;
        }
        if (this.singlePool === 0) {
          //己方无人下注
          return amount + this.doublePool;
        }
        return (amount / (amount + this.singlePool) * (amount + this.pool)).toFixed(0);
      }
      if (this.flow === 0) { //双
        if (this.singlePool === 0) {
          //对方无人下注
          return amount;
        }
        if (this.doublePool === 0) {
          //己方无人下注
          return amount + this.singlePool;
        }
        return (amount / (amount + this.doublePool) * (amount + this.pool)).toFixed(0);
      }
      return 0;
    }
  }
}
</script>

