<template>
  <div>
    <b-navbar toggleable="lg" type="white" variant="nav" style="padding: 0">
      <b-container bottom>
        <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
        <b-navbar  type="white">
          <b-navbar-brand href="#" >
            <img src="../assets/img/logo.png" alt="Kitten" id="logo-img" height="30">
          </b-navbar-brand>
        </b-navbar>
        <b-collapse id="nav-collapse" is-nav>
          <b-navbar-nav>
            <b-nav-item href="#info">{{ $t('nav.about') }}</b-nav-item>
          </b-navbar-nav>
          <b-navbar-nav>
            <b-nav-item href="#how">{{ $t('nav.tutorial') }}</b-nav-item>
          </b-navbar-nav>
          <b-navbar-nav>
            <b-nav-item href="#chips-rule2">{{ $t('nav.rule') }}</b-nav-item>
          </b-navbar-nav>
          <!--          <b-navbar-nav>-->
          <!--            <b-nav-item href="#">Link</b-nav-item>-->
          <!--            <b-nav-item href="#" disabled>Disabled</b-nav-item>-->
          <!--          </b-navbar-nav>-->

          <!-- Right aligned nav items -->

        </b-collapse>
        <b-navbar-nav class="ml-auto">
          <b-nav-item href="#">
            <div class="contact" v-if="account === ''" @click="getAccount">{{ $t('nav.contact') }}</div>
            <div class="d-flex address align-items-center" v-else>
              <b-img src="/assets/img/wallet.png" width="20" height="20">
              </b-img>
              <div class="ml-1">
                {{ hideAccount }}
              </div>
            </div>
          </b-nav-item>
        </b-navbar-nav>
      </b-container>
    </b-navbar>
  </div>
</template>

<script>
export default {
  name: "Navbar",
  data() {
    return {
      meal: '',
      account: ''
    }
  },
  created() {
    if (typeof window.ethereum !== 'undefined') {
      this.getAccount()
      ethereum.on('accountsChanged', accounts => {
        // Time to reload your interface with accounts[0]!
        this.getAccount()
      });
      ethereum.on('chainChanged', chainId => {
        /* handle the chainId */
        this.getAccount();
      });
    }
  },
  methods: {
    async getAccount() {
      if (typeof window.ethereum === 'undefined') {
        console.error('metamask not installed');
        return '';
      }
      const accounts = await ethereum.request({method: 'eth_requestAccounts'});
      this.account = accounts[0];
      this.$emit('accountChanged', this.account)
    }
  },
  computed: {
    hideAccount() {
      return this.account.substr(0, 6) + '...' + this.account.substr(-4, 4)
    }
  }
}
</script>

<style lang="scss">
