<template>
  <div class="">
    <strong class="chips-down minute">{{ minuteString }}</strong>
    <span>{{ $t('pool.minute') }}</span>
    <strong class="chips-down second">{{ secondString }}</strong>
    <span>{{ $t('pool.second') }}</span>
  </div>
</template>

<script>
import axios from 'axios'
import jsonp from 'jsonp'
import Data from "@/components/Contract";

export default {
  name: "TimerDown",
  data() {
    return {
      hour: '',
      minute: '',
      second: '',
      timer: '',
      intervalMinute: 5, //每期间隔分钟
      remainTime: 300, // 倒计时间总秒数
      tokenContract: new web3.eth.Contract(Data.tokenAbi, Data.tokenAddress), //代币合约对象,
      timestamp: 0,
      round: 0,
      roundList: []
    }
  },
  // props: {
  //   remainTime: {    // 倒计时间总秒数
  //     default: ''
  //   }
  // },
  created() {
    // axios.get('http://api.m.taobao.com/rest/api3.do?api=mtop.common.getTimestamp&callback=jsonCallBack').then(res => {
    //   console.log(res)
    // })

    // this.initTime()
    this.countDown()
  },
  methods: {
    async getRemainTime_bak() {
      let time = await this.tokenContract.methods.timestamp().call();
      time = parseInt(time) + 28800;
      // console.log('getRemainTime:' + time + ',timestamp:' + this.timestamp)
      // this.remainTime = (this.intervalMinute * 60) - time % (this.intervalMinute * 60)
      if (this.timestamp < time) {
        this.timestamp = time;
        let _remainTime = (this.intervalMinute * 60) - time % (this.intervalMinute * 60)
        let diff = Math.abs(this.remainTime - _remainTime);
        if (diff > 3) {
          this.remainTime = _remainTime;
        }
      }

      this.round = Math.floor((time % 86400) / (this.intervalMinute * 60)) + 1;
      if (typeof this.roundList[this.round] === 'undefined') {
        this.roundList[this.round] = false;
      }
    },

    async getRemainTime() {
      let res = await axios.get('/api/rest/api3.do?api=mtop.common.getTimestamp');
      let time = parseInt((res.data.data.t / 1000).toString()) + 28800;
      // console.log('getRemainTime:' + time + ',timestamp:' + this.timestamp)
      this.remainTime = (this.intervalMinute * 60) - time % (this.intervalMinute * 60)

      this.round = Math.floor((time % 86400) / (this.intervalMinute * 60)) + 1;
      if (typeof this.roundList[this.round] === 'undefined') {
        this.roundList[this.round] = false;
      }
    },


    initTime() {
      if (this.remainTime < 0) {
        this.remainTime = 0;
      }
      // this.hour = Math.floor((this.remainTime / 3600) % 24)
      this.minute = Math.floor((this.remainTime / 60) % 60)
      this.second = Math.floor(this.remainTime % 60)
    },
    async countDown() {
      clearInterval(this.timer)
      await this.getRemainTime();

      this.timer = setInterval(async () => {
        // console.log(this.remainTime);

        // if (this.remainTime > 12 && this.remainTime % 30=== 1) { //如果剩余时间大于12秒，每30秒同步时间
        //   await this.getRemainTime();
        // }
        this.initTime()
        this.$emit('timeTick', this.remainTime)

        if (this.minute <= 0 && this.second <= 0) {
          if (this.roundList[this.round] === false) {
            this.$emit('countDownEnd', true);
            this.roundList[this.round] = true;
          }
          await this.countDown();
        }
        this.remainTime--;
        // this.timestamp++;
        // console.log('timestamp:' + this.timestamp)

        // if (self.hour === 0) {
        //   if (self.minute !== 0 && self.second === 0) {
        //     self.second = 59
        //     self.minute -= 1
        //   } else if (self.minute === 0 && self.second === 0) {
        //     self.second = 0
        //     clearInterval(self.timer)
        //     self.$emit('countDownEnd', true)
        //     //重新计时
        //     self.$nextTick(() => {
        //       // self.remainTime = 599;
        //       self.initTime();
        //       self.countDown();
        //     })
        //   } else {
        //     self.second -= 1
        //   }
        // } else {
        //   if (self.minute !== 0 && self.second === 0) {
        //     self.second = 59
        //     self.minute -= 1
        //   } else if (self.minute === 0 && self.second === 0) {
        //     self.hour -= 1
        //     self.minute = 59
        //     self.second = 59
        //   } else {
        //     self.second -= 1
        //   }
        // }

        // console.log(self.remainTime);

        // self.remainTime--;
      }, 1000)
    },
    formatNum(num) {
      return num < 10 ? '0' + num : '' + num
    },
  },
  computed: {
    hourString() {
      return this.formatNum(this.hour)
    },
    minuteString() {
      return this.formatNum(this.minute)
    },
    secondString() {
      return this.formatNum(this.second)
    }
  }
}
</script>

