import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import Web3 from "web3"
import vGoogleTranslate from 'v-google-translate';
Vue.use(vGoogleTranslate)
import VueI18n from "vue-i18n";
Vue.use(VueI18n);
// 创建实例 - 根据本地语言加载多语言文件
const i18n = new VueI18n({
  // locale: 'zh-CN', // 默认语言 可以借助cookie来设置和获取本地语言环境 从而加载不同的语言文件
  locale: localStorage.lang || navigator.language,
  messages: {
    'en': require('@/assets/lang/en'),
    'zh-cn': require('@/assets/lang/zh-CN'),
    'zh-tw': require('@/assets/lang/zh-CN'),
  }
})
import BootstrapVue from "bootstrap-vue"
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import { BContainer } from 'bootstrap-vue'
Vue.component('b-container', BContainer)
Vue.use(BootstrapVue)
Vue.prototype.Web3 = Web3


if (typeof window.ethereum !== 'undefined') {
  // window.web3 = new Web3(web3.currentProvider); //metamask
  window.web3 = new Web3(window.ethereum); //metamask
} else  {
  // window.web3 = new Web3(Web3.providers.HttpProvider('https://data-seed-prebsc-1-s1.binance.org:8545/'))
  window.web3 = new Web3("wss://bsc-ws-node.nariox.org:443")
  // window.web3 = new Web3('https://bsc-dataseed.binance.org/')
  // window.web3 = new Web3('https://data-seed-prebsc-1-s1.binance.org:8545/')
}


Vue.config.productionTip = false

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
