<template>
  <div>
    <b-carousel
        id="carousel"
        v-model="slide"
        fade
        :interval="3500"
        label-goto-slide
        label-indicators
        indicators
        img-width="1920"
        img-height="90"
        style="text-shadow: 1px 1px 2px #333;"
        @sliding-start="onSlideStart"
        @sliding-end="onSlideEnd"
    >
      <b-carousel-slide
          img-src="/assets/img/banner.png"
      ></b-carousel-slide>
      <b-carousel-slide
          img-src="/assets/img/banner.png"
      ></b-carousel-slide>
      <b-carousel-slide
          img-src="/assets/img/banner.png"
      ></b-carousel-slide>
    </b-carousel>
  </div>
</template>

<script>
export default {
  name: "Carousel",
  data() {
    return {
      slide: 0,
      sliding: null
    }
  },
  methods: {
    onSlideStart(slide) {
      this.sliding = true
    },
    onSlideEnd(slide) {
      this.sliding = false
    }
  }
}
</script>

